html,
body {
  height: 100%; }

* {
  box-sizing: border-box; }
  *::before, *::after {
    box-sizing: border-box; }

body {
  font-size: 16px;
  line-height: 1.4;
  color: #4C4C4C;
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
  min-width: 320px; }
  body input,
  body textarea,
  body button {
    outline: none; }
    body input:focus,
    body textarea:focus,
    body button:focus {
      outline: none; }

.site {
  display: flex;
  flex-direction: column;
  min-height: 100%; }
  .site .site-top {
    flex: 1 0 auto; }
  .site .site-bottom {
    flex: 0 0 auto; }

.shell {
  max-width: 1134px;
  padding-left: 17px;
  padding-right: 17px;
  margin-left: auto;
  margin-right: auto; }

.top-line {
  background-color: #ffffff;
  border-bottom: 1px solid #ECECEC;
  padding: 50px 0;
  padding-bottom: 49px; }
  .top-line .top-line__content {
    display: flex;
    align-items: center; }

.logo {
  margin-right: auto; }
  .logo img {
    display: block; }

.nav-top ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin-left: -10px;
  margin-right: -10px; }
  .nav-top ul li {
    padding-left: 10px;
    padding-right: 10px; }
    .nav-top ul li a {
      display: block;
      padding: 0.393em 20px;
      line-height: 1;
      text-decoration: none;
      color: #000;
      font-weight: 600;
      border-radius: 30px;
      font-size: 14px; }
      .nav-top ul li a:hover {
        text-decoration: underline; }
      .nav-top ul li a.active {
        background-color: #FF4778;
        color: #ffffff; }
        .nav-top ul li a.active:hover {
          text-decoration: none;
          opacity: 0.8; }

.user .user__ava {
  display: block;
  width: 40px;
  height: 40px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 2em;
